@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans&family=Montserrat:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:wght@600&family=Merriweather+Sans&family=Montserrat:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#landing{
    height : 100vh;
    width : 100vw; 
    display : flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

#left{
    width: 500px;
    height: 500px;
    display: flex;
    flex-direction: column;
    text-align: left;
}

#landing h2{
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    text-transform: uppercase;

    color: #3EB9FF;
}

#landing p{
    font-family: 'Merriweather Sans', sans-serif;
    font-style: normal;
    font-size: 16px;
    line-height: 24px;
    width: 350px;
    width: 373px;

    color: #3F3D56;
}

#landing button{
    width: 282px;
    height: 76px;

    background: #3EB9FF;
    border-radius: 10px;
    border : none;

    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    cursor: pointer;
    /* identical to box height */

    margin-top: auto;
    color: #FFFFFF;
}

#landing button:hover{
    background: #3F3D56;
    
}
#header 
{
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    padding: 10px;
    border-radius: 50px;
    width: 70px;
    margin: 15px 40px;
    background: rgba(62, 185, 255, 1);
    z-index: 10;
}

#header.faded
{
    background: rgba(62, 185, 255, 0.3);
}

.header-logo
{
    height: 50px;
}

#header-p{
    font-family: 'Lora', serif;
    color: #3EB9FF;
    font-size: 64px;
    margin: 0;
    margin-top: 0;
    text-align: left;
    padding-top: 16px;
    margin-left: 140px;
}

.headerContainer{
    position: absolute;
}
#video{
    opacity: 1;
    z-index: 1;
  }
  
  #video2{
    opacity: 0;
    z-index: 2;
  }
  #video, #video2{
    position: absolute;
  }
  
  .fullVideo{
    height: auto;
    width: 100vw;
    transition: opacity 2.0s ease-in; 
  }
  
  html, body {
    overflow-y:hidden;
    height:100%;
  }
  
  .next {
    position: absolute;
    top: 35px;
    z-index: 15;
  }
  
  .left{
    left: 70px;
  }
  
  .right{
    right: 70px;
  }
  
  .controlArea{
    width: 440px;
    height: 140px;
    position: absolute;
    background: rgba(62, 185, 255, 0.3);
    border-radius: 20px;
    bottom: 60px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    padding: 0 40px;
  }
.volumeContainer{
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
